import React from  'react';
import {connect} from 'react-redux';
import styles from './rates.module.css';

import RatesList from './components/RatesList/RatesList';
import Loading from '../../../../components/loading';
import {rateList} from '../../../../general_redux/rates/actions';
import Taxes from './components/Taxes/Taxes';
import Fees from './components/Fees/Fees';

class Rates extends React.Component{
    state={
        currentTab:"Rates",
        tabs:[
            "Rates",
            "Fees",
            "Taxes"
        ],
    }
    handleTab = (tab)=>{
        this.setState({
            currentTab:tab
        })
    }
    componentDidMount() {
        const {rateList} =this.props;
        rateList();
    }

    render(){
        const {currentTab,tabs}=this.state;
        const {rateLoadStatus}=this.props;
        return(
            <div>
                <Loading loadingStatus={rateLoadStatus.loading}/>
                <div className={styles.ratesHeader}>
                    {tabs.length!==0 && tabs.map((tab,index)=>( 
                        <div className={styles.tabBox} key={index} onClick={()=>this.handleTab(tab)}>
                            {tab}
                        </div>
                    ))}
                </div>
                <div style={{margin:'0em 1em'}}>
                    {currentTab==="Rates" && (
                        <RatesList/>
                    )}
                    {currentTab==="Taxes" && (
                        <Taxes/>
                    )}
                    {currentTab==="Fees" && (
                        <Fees/>
                    )}
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state)=>({
    rateLoadStatus: state.rates.actionForRateList
})
const mapDispatchToProps = dispatch => ({
    rateList: (data)=>{
        dispatch(rateList(data))
    }
});

export default connect(mapStateToProps,mapDispatchToProps)(Rates);