import {
  LOAD_INQUIRIES,
} from './types' ;
import {BACKEND_API} from "../../config/middleware";
import API from "../../config/api-url";

export const LoadInquiries = () => ({
  type: BACKEND_API,
  payload: Object.assign(
      {
          type:LOAD_INQUIRIES,
          method: 'get',
          url: API.INQUIRY,
      },
  )
});
