import React, { useState, useEffect } from 'react';
import { Button } from 'react-bootstrap';
import ConfirmMessage from '../../../../components/Confirmation';
import styles from './usersetting.module.css';
import Loading from '../../../../components/loading';
import { addUserSettings, getUserInfo, deleteUser } from '../../../../general_redux/user/actions';
import { useDispatch, useSelector } from 'react-redux';

function UserSettings() {
  const dispatch = useDispatch();
  const [deleteModal, setDeleteModal] = useState(false);
  const user = useSelector(({ user }) => user && user.user);
  const { useSettingUpdate, useSettingLoad, deleteUserAction } = useSelector(({ user }) => user);

  useEffect(() => {
    dispatch(getUserInfo());
  }, []);

  const handleClose = () => setDeleteModal(false);
  const handleShow = () => setDeleteModal(true);

  const handleSubmit = (e) => {
    e.preventDefault();
    const userSettingsData = {
      name: e.target.name.value,
      emailId: e.target.emailId.value,
      language: e.target.language.value,
      permissions: e.target.permissions.value,
      accountType: e.target.accountType.value,
      // twoYearBookingWindow: e.target.twoYearBookingWindow.checked,
      numberOf: e.target.numberOf.value ? e.target.numberOf.value : 1,
      // allowBookingUnit: e.target.allowBookingUnit.value,
      isFutureBooking: e.target.futureBookingWindow.checked ? e.target.futureBookingWindow.checked : 'years',
      allowBookingFor: e.target.allowBookingUnit.value,
    };
    dispatch(addUserSettings(userSettingsData));
  };
  const deleteUserAcc = () => {
    dispatch(deleteUser(user.id));
    setDeleteModal(false);
  };

  return (
    <div className={'p-3 h-100 overflow-auto'}>
      <ConfirmMessage
        show={deleteModal}
        onHide={() => {
          setDeleteModal(false);
        }}
        confirmHeader={`Are you sure you want to delete your account?`}
        confirmBody={`Deleting your account is permanent and will remove all content including bookings 
            and profile settings. This action cannot be undone. 
            Are you sure you want to delete your account?`}
        onConfirmAct={deleteUserAcc}
      />
      <Loading loadingStatus={useSettingUpdate.loading || useSettingLoad.loading || deleteUserAction.loading} />

      <div className={styles.usersettingHeader}>User Settings</div>
      <form onSubmit={(e) => handleSubmit(e)}>
        <div className="form-group row">
          <label htmlFor="name" className="col-sm-2 col-form-label">
            Name
          </label>
          <div className="col-md-4">
            <input type="text" className="form-control" id="name" style={{fontSize:'inherit'}} defaultValue={user.name} required />
          </div>
        </div>

        <div className="form-group row">
          <label htmlFor="emailId" className="col-sm-2 col-form-label">
            Email
          </label>
          <div className="col-md-4">
            <input type="email" className="form-control" id="emailId" style={{fontSize:'inherit'}} defaultValue={user.emailId} required />
          </div>
        </div>

        <div className="form-group row">
          <label htmlFor="language" className="col-sm-2 col-form-label">
            Language
          </label>
          <div className="col-md-4">
            <select id="language" className="form-control" disabled style={{fontSize:'inherit'}}>
              <option defaultValue>{user.language}</option>
            </select>
          </div>
        </div>

        <div className="form-group row">
          <label htmlFor="permissions" className="col-sm-2 col-form-label">
            User Permission
          </label>
          <div className="col-md-4">
            <input type="text" className="form-control" id="permissions" style={{fontSize:'inherit'}} defaultValue={'Admin'} disabled />
          </div>
        </div>

        <div className={styles.usersettingHeader}>Calendar settings</div>

        <div className="form-group row">
          <label htmlFor="twoYearBookingWindow" className="col-sm-2 col-form-label">
            Future Booking Window:{' '}
          </label>
          <div className="col-sm-2">
            <div className="form-check">
              <label>2 years</label>
              <input
                style={{ marginTop: '0.5em', marginLeft: '0.5em' }}
                className="form-check-input position-static"
                type="checkbox"
                id="twoYearBookingWindow"
                name={'futureBookingWindow'}
                defaultChecked={user.isFutureBooking}
              />
            </div>
          </div>
        </div>
        <div className="form-group row">
          <label htmlFor="numberOf" className="col-sm-2 col-form-label">
            Allow booking for next{' '}
          </label>
          <div className="col-sm-2">
            <div className="form-check">
              <label>month(s)</label>
              <input
                  style={{ marginTop: '0.5em', marginLeft: '0.5em' }}
                  className="form-check-input position-static"
                  type="radio"
                  id="allowBookingUnit"
                  name="allowBookingUnit"
                  value="months"
                  defaultChecked={user.allowBookingFor == 'months'}
              />
              <label style={{ marginLeft: '0.7em' }}>year(s)</label>
              <input
                  style={{ marginTop: '0.5em', marginLeft: '0.5em' }}
                  className="form-check-input position-static"
                  type="radio"
                  id="allowBookingUnit"
                  name="allowBookingUnit"
                  value="years"
                  defaultChecked={user.allowBookingFor == 'years'}
              />
            </div>
          </div>
          <div className="col-md-1 pr-0">
            <input type="number" className="form-control" style={{fontSize:'inherit'}} id="numberOf" min="1" defaultValue={user.numberOf} />
          </div>


        </div>

        <div className={styles.usersettingHeader}>Account Settings</div>

        <div className="form-group row">
          <label htmlFor="accountType" className="col-sm-2 col-form-label">
            Account Type
          </label>
          <div className="col-md-4">
            <input type="text" className="form-control" style={{fontSize:'inherit'}} id="accountType" defaultValue={'Free'} disabled />
          </div>
        </div>
        <br />
        <button className={styles.savBtn} type="submit">
          Save changes
        </button>
        <Button className={styles.delBtn} onClick={handleShow}>
          Delete Account
        </Button>
      </form>
    </div>
  );
}
export default UserSettings;
