import {
    ADD_BOOKING,
    EDIT_BOOKING,
    LOAD_BOOKING,
    BOOKING_FLAG,
    CLEAR_BOOKING,
} from './types';
import {BACKEND_API} from "../../config/middleware";
import API from "../../config/api-url";

export const addNewBooking = data => ({
    type: BACKEND_API,
    payload: Object.assign(
        {
            type:ADD_BOOKING,
            method: 'post',
            url: API.ADD_BOOKING,
            data
        },
    )
});

export const editBooking=data=>({
    type: BACKEND_API,
    payload: Object.assign(
        {
            type:EDIT_BOOKING,
            method: 'put',
            url: API.EDIT_BOOKING,
            data
        },
    )
})

export const loadBooking=()=>({
    type: BACKEND_API,
    payload: Object.assign(
        {
            type:LOAD_BOOKING,
            method: 'get',
            url: API.BOOKING,
        },
    )
})

export const resetBookingFlag=()=>({
    type:BOOKING_FLAG
})

export const clearBooking=()=>({
    type:CLEAR_BOOKING
})