import React, { useEffect, useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import { Formik } from 'formik';

function DefaultChangeForm({ schema, handleSubmit, options, rateList }) {
  const [rental, setRental] = useState('');
  const [isFixedRate, setIsFixedRate] = useState();
  const [isDiscounted, setIsDiscounted] = useState();
  const [selectedRate, setSelectedRate] = useState(false);

  console.log('IS FIXED RATE: ',isFixedRate);
  console.log('IS DISCOUNTED: ',isDiscounted);
  console.log('RATE LIST', rateList);

  const initValue = {
    rentalOrGroup: rental,
    guestFee: selectedRate ? selectedRate.usdPerGuest : '',
    dailyRate: selectedRate ? selectedRate.dailyRate : '',
    discount: selectedRate ? selectedRate.allowDiscount : false,
    fixRate: selectedRate ? selectedRate.allowFixedRate : false,
    guestLimit: selectedRate ? selectedRate.guestPerNight : '',
    minStay: selectedRate ? selectedRate.minimumStayRequirement : '',
    monthDiscount: selectedRate ? selectedRate.monthlyDiscount : '',
    monthPrice: selectedRate ? selectedRate.monthlyPrice : '',
    weekDiscount: selectedRate ? selectedRate.weeklyDiscount : '',
    weekPrice: selectedRate ? selectedRate.weekPrice : '',
  };

  useEffect(() => {
    rateList.map((rate) => {
      if (rate.id == rental) {
        setSelectedRate(rate);
        if (rate.allowDiscount) {
          setIsFixedRate(false);
          setIsDiscounted(true);
        } else if (rate.allowFixedRate) {
          setIsFixedRate(true);
          setIsDiscounted(false);
        } else {
          setIsFixedRate(false);
          setIsDiscounted(false);
        }
    }})
  }, [rental]);


  return (
    <>
      <Formik
        validationSchema={schema}
        initialValues={initValue}
        onSubmit={handleSubmit}
        validateOnChange={false}
        validateOnBlur={false}
        enableReinitialize={true}
      >
        {({ values, errors, handleChange, handleSubmit, initialValues}) => (
          <Form onSubmit={handleSubmit}>
            <Form.Row>
              <Form.Label className="col-sm-4 col-form-label">Select Rental</Form.Label>
              <div className="col-md-4">
                <Form.Control
                  as="select"
                  name="rentalOrGroup"
                  onChange={handleChange}
                  isInvalid={!!errors.rentalOrGroup}
                >
                  <option key={'selectR'} selected={true} disabled={true}>
                    ----Select----
                  </option>
                  {options.map((opt, index) => (
                    <option key={index} value={opt.id}>
                      {opt.name}
                    </option>
                  ))}
                </Form.Control>
                <Form.Control.Feedback type="invalid">{errors.rentalOrGroup}</Form.Control.Feedback>
              </div>
            </Form.Row>
            <br />
            <Form.Row>
              <Form.Label className="col-sm-4 col-form-label">Default rate per night</Form.Label>
              <div className="col-md-4">
                <Form.Control
                  type="number"
                  name="dailyRate"
                  min={55}
                  onChange={handleChange}
                  isInvalid={!!errors.dailyRate}
                  defaultValue={initialValues.dailyRate}
                />
                <Form.Control.Feedback type="invalid">{errors.dailyRate}</Form.Control.Feedback>
              </div>
            </Form.Row>
            <br />
            <Form.Row>
              <Form.Label className="col-sm-4 col-form-label">Default Min stay</Form.Label>
              <div className="col-md-4">
                <Form.Control
                  type="number"
                  name="minStay"
                  min={0}
                  max={15}
                  onChange={handleChange}
                  isInvalid={!!errors.minStay}
                  defaultValue={initialValues.minStay}
                />
                <Form.Control.Feedback type="invalid">{errors.minStay}</Form.Control.Feedback>
              </div>
            </Form.Row>
            <br />
            <Form.Row>
              <Form.Label className="col-sm-4 col-form-label">Default Rate per guest</Form.Label>
              <div className="col-md-4">
                <Form.Control
                  type="number"
                  name="guestFee"
                  min={0}
                  onChange={handleChange}
                  isInvalid={!!errors.guestFee}
                  defaultValue={initialValues.guestFee}
                />
                <Form.Control.Feedback type="invalid">{errors.guestFee}</Form.Control.Feedback>
              </div>
            </Form.Row>
            <br />
            <Form.Row>
              <Form.Label className="col-sm-4 col-form-label">Guest Limit</Form.Label>
              <div className="col-md-4">
                <Form.Control
                  type="number"
                  name="guestLimit"
                  min={0}
                  onChange={handleChange}
                  isInvalid={!!errors.guestLimit}
                  defaultValue={initialValues.guestLimit}
                />
                <Form.Control.Feedback type="invalid">{errors.guestLimit}</Form.Control.Feedback>
              </div>
            </Form.Row>
            <br />
            <Form.Row>
              <Form.Group>
                <Form.Check
                  type={'checkbox'}
                  value="true"
                  label="Allow discount"
                  name="discount"
                  onChange={handleChange}
                  defaultChecked={selectedRate.allowDiscount}
                  disabled={isFixedRate}
                />
              </Form.Group>
            </Form.Row>
            <br />
            <Form.Row>
              <Form.Label className="col-sm-4 col-form-label">Weekly discount</Form.Label>
              <div className="col-md-4">
                <Form.Control
                  type="number"
                  step="0.01"
                  name="weekDiscount"
                  min={0}
                  onChange={handleChange}
                  isInvalid={!!errors.weekDiscount}
                  defaultValue={isDiscounted ? initialValues.weekDiscount : 0}
                  disabled={!isDiscounted || isFixedRate}
                />
                <Form.Control.Feedback type="invalid">{errors.weekDiscount}</Form.Control.Feedback>
              </div>
            </Form.Row>
            <br />
            <Form.Row>
              <Form.Label className="col-sm-4 col-form-label">Monthly discount</Form.Label>
              <div className="col-md-4">
                <Form.Control
                  type="number"
                  step="0.01"
                  name="monthDiscount"
                  min={values.weekDiscount * 4}
                  onChange={handleChange}
                  isInvalid={!!errors.monthDiscount}
                  defaultValue={isDiscounted ? initialValues.monthDiscount : 0}
                  disabled={!isDiscounted || isFixedRate}
                />
                <Form.Control.Feedback type="invalid">{errors.monthDiscount}</Form.Control.Feedback>
              </div>
            </Form.Row>
            <br />
            <Form.Row>
              <Form.Group>
                <Form.Check
                  type={'checkbox'}
                  value="true"
                  label="Allow Fixed Rate"
                  name="fixRate"
                  onChange={handleChange}
                  defaultChecked={selectedRate.allowFixedRate}
                  disabled={isDiscounted}
                />
              </Form.Group>
            </Form.Row>
            <br />
            <Form.Row>
              <Form.Label className="col-sm-4 col-form-label">Weekly Price</Form.Label>
              <div className="col-md-4">
                <Form.Control
                  type="number"
                  name="weekPrice"
                  min={0}
                  onChange={handleChange}
                  isInvalid={!!errors.weekPrice}
                  defaultValue={isFixedRate ? initialValues.weekPrice : 0}
                  disabled={isDiscounted || !isFixedRate}
                />
                <Form.Control.Feedback type="invalid">{errors.weekPrice}</Form.Control.Feedback>
              </div>
            </Form.Row>
            <br />
            <Form.Row>
              <Form.Label className="col-sm-4 col-form-label">Monthly Price</Form.Label>
              <div className="col-md-4">
                <Form.Control
                  type="number"
                  name="monthPrice"
                  min={values.weekPrice * 4}
                  onChange={handleChange}
                  isInvalid={!!errors.monthPrice}
                  defaultValue={isFixedRate ? initialValues.monthPrice : 0}
                  disabled={isDiscounted || !isFixedRate}
                />
                <Form.Control.Feedback type="invalid">{errors.monthPrice}</Form.Control.Feedback>
              </div>
            </Form.Row>
            <br />
            <div className={`{styles.modalFooter} p-2 d-flex justify-content-center `}>
              <Button variant="primary" disabled={!rental} type="submit" className={'col-lg-4 p-2 mr-4 col-sm-12'}>
                Save changes
              </Button>
            </div>
            {setRental(values.rentalOrGroup)}
            {setIsDiscounted(values.discount)}
            {setIsDiscounted(values.discount)}
            {setIsFixedRate(values.fixRate)}
          </Form>
        )}
      </Formik>
    </>
  );
}

export default DefaultChangeForm;
