import React, { useState, useMemo } from 'react';
import { Link } from 'react-router-dom';
import countryList from 'react-select-country-list';
import Select from 'react-select';
import { useSelector, useDispatch } from 'react-redux';
import styles from './register.module.css';
import Header from '../Header/Header';
import Footer from '../Footer/Footer';
import logo from '../../icons/logo.png';
import { registerUser } from '../../general_redux/user/actions';
import Loading from '../../components/loading';
import {currencies} from '../../config/data/currencies';
import ReactTooltip from 'react-tooltip';
import ContactUS from '../Home/Containers/ContactUs';
import ContactUs from "../Home/Containers/ContactUs";

function Register() {
  const [viewContactUs, setViewContactUs] = useState(false);
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [company, setCompanyName] = useState('');
  const [website, setWebsite] = useState('');
  const [propertyType, setPropertyType] = useState('');
  const [numberOfUnits, setNumberOfUnits] = useState('');
  const [defaultCurrency, setDefaultCurrency] = useState({value:"USD",label:"USD"});
  const [country, setCountry] = useState('');
  const [checkInTime, setCheckInTime] = useState('');
  const [checkOutTime, setCheckOutTime] = useState('');
  const [ratePerNight, setRatePerNight] = useState('');
  const [minimumStay, setMinimumStay] = useState('');
  const [pageNumber, setPageNumber] = useState(1);

  const registerStatus = useSelector(({ user }) => user && user.registerAction);
  const dispatch = useDispatch();
  const options = useMemo(() => countryList().getData(), []);
  
  const customTheme = {
    control: (provided) => ({
      ...provided,
      border: 'none',
      boxShadow: 'none',
      backgroundColor: 'transparent',
    }),
    menu: (provided) => ({
      ...provided,
      border: '1px solid lightgrey',
      boxShadow: 1,
    }),
  };

  const propertyOptions = [
    { value: 'serviced_apartments', label: 'Serviced Apartments' },
    { value: 'bnb', label: 'B&B' },
    { value: 'hotel', label: 'Hotel' },
    { value: 'other', label: 'Other' },
  ];

  function handleNextPage(e){
    e.preventDefault();
    setPageNumber(2);
  }

  function handleSubmit(e) {
    e.preventDefault();
    const data = {
      name: `${firstName} ${lastName}`,
      emailId: email,
      companyName: company,
      website: website,
      propertyType: propertyType,
      noOfUnits: numberOfUnits,
      currency: (defaultCurrency)?defaultCurrency.label:"",
      country: country ? country.label : "",
      checkInTime: checkInTime,
      checkOutTime: checkOutTime,
      dailyRate: ratePerNight,
      minimumStayRequirement: minimumStay,
    };
    dispatch(registerUser(data));
  }


  if (pageNumber === 2) {
    return (
      <div>
        <Loading  loadingStatus={registerStatus.loading}/>
        <Header/>
        <ContactUs
            show={viewContactUs}
            onHide={() => {
              setViewContactUs(false);
            }}
        />
        <div className="d-flex justify-content-center">
          <div className={`${styles.mainContainer} ${styles.register}`}>
            <div className={`${styles.register_wrapper} shadow`}>
              <div className="d-flex justify-content-center">
                <Link to="/">
                  <img src={logo} className={styles.register_image} />
                </Link>
              </div>
              <div className={`d-flex justify-content-center ${styles.register_header}`}>
                <h5>Account Set Up</h5>
              </div>
              <div className="d-flex justify-content-center">
                <form onSubmit={handleSubmit}>
  
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <label className={`input-group-text ${styles.input_group_text}`}>Currency: </label>
                    </div>
                    <div className={`form-control ${styles.form_control} ${styles.selector}`}>
                      <Select
                          options={currencies}
                          value={defaultCurrency}
                          onChange={(val)=>setDefaultCurrency(val)}
                          styles={customTheme}
                      />
                    </div>
                    <div className={styles.toolTipIconContainer}>
                    </div>
                  </div>
  
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <span className={`input-group-text ${styles.input_group_text}`}>Check-in time: </span>
                    </div>
                    <input
                      onChange={(e) => setCheckInTime(e.target.value)}
                      type="time"
                      required
                      value={checkInTime}
                      className={`form-control ${styles.form_control}`}
                    />
                    <div className={styles.toolTipIconContainer}>
                      <i className={`fa fa-question-circle ${styles.toolTipIcon}`} data-tip data-for='checkInTime'/>
                      <ReactTooltip place="left" type="dark" id='checkInTime' effect="solid">
                        <span>Set the default check-in time.<br /> You will be able to change this later.</span>
                      </ReactTooltip>
                    </div>
                  </div>
  
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <span className={`input-group-text ${styles.input_group_text}`}>Check-out time: </span>
                    </div>
                    <input
                      onChange={(e) => setCheckOutTime(e.target.value)}
                      type="time"
                      required
                      value={checkOutTime}
                      className={`form-control ${styles.form_control}`}
                    />
                    <div className={styles.toolTipIconContainer}>
                      <i className={`fa fa-question-circle ${styles.toolTipIcon}`} data-tip data-for='checkOutTime'/>
                      <ReactTooltip place="left" type="dark" id='checkOutTime' effect="solid">
                        <span>Set the default check-out time.<br /> You will be able to change this later.</span>
                      </ReactTooltip>
                    </div>
                  </div>
  
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <span className={`input-group-text ${styles.input_group_text}`}>Daily rate: </span>
                    </div>
                    <input
                      onChange={(e) => setRatePerNight(e.target.value)}
                      type="number"
                      required
                      value={ratePerNight}
                      className={`form-control ${styles.form_control}`}
                      min={1}
                    />
                    <div className={styles.toolTipIconContainer}>
                      <i className={`fa fa-question-circle ${styles.toolTipIcon}`} data-tip data-for='ratePerNight'/>
                      <ReactTooltip place="left" type="dark" id='ratePerNight' effect="solid">
                        <span>Set the default rate per rental per night.<br /> You will be able to change this later.</span>
                      </ReactTooltip>
                    </div>
                  </div>
  
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <span className={`input-group-text ${styles.input_group_text}`}>Minimum stay: </span>
                    </div>
                    <input
                      onChange={(e) => setMinimumStay(e.target.value)}
                      type="number"
                      required
                      value={minimumStay}
                      className={`form-control ${styles.form_control}`}
                      min={1}
                    />
                    <div className={styles.toolTipIconContainer}>
                      <i className={`fa fa-question-circle ${styles.toolTipIcon}`} data-tip data-for='minimumStay'/>
                      <ReactTooltip place="left" type="dark" id='minimumStay' effect="solid">
                        <span>The minimum stay requirement is <br />the minimum number of nights that <br />a guest can book a rental. Don't worry, <br /> you will be able to change this <br />information later.</span>
                      </ReactTooltip>
                    </div>
                  </div>
  
                  <div className="d-flex justify-content-start">
                    <button className={styles.register_back} onClick={ () => setPageNumber(1)}>&lt;&lt;&nbsp; back</button>
                  </div>
  
                  <div className="d-flex justify-content-center">
                    <button className={styles.register_submit}>Submit</button>
                  </div>
  
                </form>
              </div>
              <div className="d-flex justify-content-center">
                <Link to="/login" className={styles.back_to_home}>
                  Cancel
                </Link>
              </div>
            </div>
          </div>
        </div>
        <Footer setViewContactUs={() => setViewContactUs(true)} />
      </div>
    );
  } else {
    return (
      <div>
        <Loading  loadingStatus={registerStatus.loading}/>
        <Header/>
        <ContactUs
            show={viewContactUs}
            onHide={() => {
              setViewContactUs(false);
            }}
        />
        <div className="d-flex justify-content-center">
          <div className={`${styles.mainContainer} ${styles.register}`}>
            <div className={`${styles.register_wrapper} shadow`}>
              <div className="d-flex justify-content-center">
                <Link to="/">
                  <img src={logo} className={styles.register_image} />
                </Link>
              </div>
              <div className={`d-flex justify-content-center ${styles.register_header}`}>
                <h5>Account Set Up</h5>
              </div>
              <div className="d-flex justify-content-center">
                <form onSubmit={handleNextPage}>
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <span className={`input-group-text ${styles.input_group_text}`}>First name: </span>
                    </div>
                    <input
                      onChange={(e) => setFirstName(e.target.value)}
                      type="text"
                      required
                      className={`form-control ${styles.form_control}`}
                      aria-label="firstName"
                      aria-describedby="firstName"
                      value={firstName}
                    />
                    <div className={styles.toolTipIconContainer}>
                    </div>
                  </div>
  
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <span className={`input-group-text ${styles.input_group_text}`}>Last name:</span>
                    </div>
                    <input
                      onChange={(e) => setLastName(e.target.value)}
                      type="text"
                      required
                      className={`form-control ${styles.form_control}`}
                      aria-label="lastName"
                      aria-describedby="lastName"
                      value={lastName}
                    />
                    <div className={styles.toolTipIconContainer}>
                    </div>
                  </div>
  
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <span className={`input-group-text ${styles.input_group_text}`}>Email address:</span>
                    </div>
                    <input
                      onChange={(e) => setEmail(e.target.value)}
                      type="email"
                      required
                      className={`form-control ${styles.form_control}`}
                      aria-label="emailAddress"
                      aria-describedby="emailAddress"
                      value={email}
                    />
                    <div className={styles.toolTipIconContainer}>
                    </div>
                  </div>
  
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <span className={`input-group-text ${styles.input_group_text}`}>Company name: </span>
                    </div>
                    <input
                      onChange={(e) => setCompanyName(e.target.value)}
                      type="text"
                      required
                      className={`form-control ${styles.form_control}`}
                      aria-label="companyName"
                      aria-describedby="companyName"
                      value={company}
                    />
                    <div className={styles.toolTipIconContainer}>
                    </div>
                  </div>
  
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <span className={`input-group-text ${styles.input_group_text}`}>Website: </span>
                    </div>
                    <input
                      onChange={(e) => setWebsite(e.target.value)}
                      type="text"
                      className={`form-control ${styles.form_control}`}
                      aria-label="website"
                      aria-describedby="website"
                      value={website}
                    />
                    <div className={styles.toolTipIconContainer}>
                    </div>
                  </div>
  
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <label className={`input-group-text ${styles.input_group_text}`}>Property type:</label>
                    </div>
                      <select
                        className={`form-control ${styles.form_control}`}
                        aria-label="propertyType"
                        aria-describedby="propertyType"
                        required={true}
                        value={propertyType}
                        onChange={(e)=>setPropertyType(e.target.value)}
                      >
                        <option value={""} key={"selectProp"} disabled={true}>{"Select property"}</option>
                        {propertyOptions.map((opt)=>(
                            <option value={opt.value} key={opt.value}>{opt.label}</option>))}
                      </select>
                      <div className={styles.toolTipIconContainer}>
                      </div>
                  </div>
  
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <span className={`input-group-text ${styles.input_group_text}`}>Number of units: </span>
                    </div>
                    <input
                      onChange={(e) => setNumberOfUnits(e.target.value)}
                      type="number"
                      required
                      className={`form-control ${styles.form_control}`}
                      aria-label="numberOfUnits"
                      aria-describedby="numberOfUnits"
                      min={1}
                      max={10}
                      value={numberOfUnits}
                    />
                    <div className={styles.toolTipIconContainer}>
                    </div>
                  </div>
  
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                      <label className={`input-group-text ${styles.input_group_text}`}>Select country:</label>
                    </div>
  
                    <div className={`form-control ${styles.form_control} ${styles.selector}`}>
                      <Select
                        options={options}
                        value={country}
                        onChange={(value) => setCountry(value)}
                        styles={customTheme}
                      />
                    </div>
                    <div className={styles.toolTipIconContainer}>
                    </div>
                  </div>
  
                  <div className="d-flex justify-content-end">
                    <button className={styles.register_next} type='submit'>Next&nbsp;&gt;&gt;</button>
                  </div>  
                </form>
              </div>
              <div className="d-flex justify-content-center">
                <Link to="/login" className={styles.back_to_home}>
                  Cancel
                </Link>
              </div>
            </div>
          </div>
        </div>
        <Footer setViewContactUs={() => setViewContactUs(true)}/>
      </div>
    );
  }
}

export default Register;
