import {toast} from "react-toastify";

import {
  LOAD_INQUIRIES,
} from './types' ;

import {actionForState} from "../../common/functions/utils";
toast.configure();

const INITIAL_STATE = {
  inquiryData: [
    {
      bookingNumber: '1004',
      channel: 'Direct',
      rental: 'Rental 1',
      guestName: 'Robert Smith',
      arrive:"31-02-2021",
      depart:"04-04-2021",
      checkInTime: '2020-12-15',
      checkOutTime: '2020-12-18',
      nights: '3',
      netAmount: '100',
      created: '15/12/2020',
      payment_status: 'Paid',
      status: 'Booked',
      rentalId:1
    },
    {
      bookingNumber: '1006',
      channel: 'Direct',
      rental: 'Rental 2',
      arrive:"26-03-2021",
      depart:"30-03-2021",
      guestName: 'Catherine Shill',
      checkInTime: '2021-01-01',
      checkOutTime: '2021-01-10',
      nights: '9',
      netAmount: '900',
      created: '12/11/2021',
      payment_status: 'Not Paid',
      status: 'Cancelled',
      rentalId:4
    },
  ],
  action:{
    loading:false,
    success:undefined
  },
  actionForInquiries:{
    loading:false,
    success:false,
    error:false
  }
};

export default function (state = INITIAL_STATE, action) {
  switch (action.type) {
    case LOAD_INQUIRIES.pending:
      return{
        ...state,
        actionForInquiries: actionForState(state.action, 'pending'),
      }
    case LOAD_INQUIRIES.success:
      return{
        ...state,
        inquiryData:action.payload,  
        actionForInquiries: actionForState(state.action,'success')
      }
    case LOAD_INQUIRIES.error:
      toast.error(action.payload.message)
      return{
        ...state,
        actionForInquiries: actionForState(state.action,'error',action.payload.message)
      }
    default:
      return state;
  }
}
