import moment from 'moment'
import Timeline, {
  TimelineHeaders,
  SidebarHeader,
  DateHeader,
  TimelineMarkers,
  CustomMarker,
  TodayMarker 
} from "react-calendar-timeline/lib";
import containerResizeDetector from 'react-calendar-timeline/lib/resize-detector/container'
// make sure you include the timeline stylesheet or the timeline will not be styled
import 'react-calendar-timeline/lib/Timeline.css'
import styles from "./calendar.module.css";
import leftArrow from '../../icons/arrow-left-square-fill.svg';

const getColour = (color) => {
  switch(color) {
    case 'red': 
    return styles.groupColourRed
    case 'yellow': 
    return styles.groupColourYellow
    case 'green': 
    return styles.groupColourGreen
  }
}

const groupRenderer = ({ group }) => {
  const color = getColour(group.colour);
  return (
    <div className={`${styles.customGroup} ${color}`}>
      <span className="title">{group.title}</span>
      <p className="tip">{group.tip}</p>
    </div>
  )
}

const itemRenderer = ({
  item,
  itemContext,
  getItemProps,
  getResizeProps,
}) => {
  const { left: leftResizeProps, right: rightResizeProps } = getResizeProps()
  return (
    <div {...getItemProps(item.itemProps)} className={styles.itemBlock} >
      {itemContext.useResizeHandle ? <div {...leftResizeProps} /> : ''}
      {item.type==="booking" && (
        <div
        className={styles.rctItemContent}
        style={{ maxHeight: `${itemContext.dimensions.height}`,borderLeft:`5px solid ${item.color}` }}
        >
           <span className={styles.itemText}>{item.title}</span>
            <span className={styles.itemStatus} style={{backgroundColor: `${item.color}`}}/>
          </div>
      )}
      {item.type==="blockdates" && (
        <div className={styles.blockdate}>
          <span>{item.title}</span>
        </div>
      )}
      {itemContext.useResizeHandle ? <div {...rightResizeProps} /> : ''}
    </div>
  )}

const MyCalendar = props => {
  console.log(moment(props.selectedDate).format('DD-MM-YYYY'));
  return (
    <div>
        {((props.groups.length!==0 && props.items.length!==0) ||
            (props.groups.length!==0 && props.items.length===0) ||
            (props.groups.length===0 && props.items.length===0)) && (
            <Timeline
                groups={props.groups}
                items={props.items}
                visibleTimeStart={moment(props.selectedDate).add(-(props.totalDays), 'day')}
                visibleTimeEnd={moment(props.selectedDate).add((props.totalDays - 1), 'day')}
                groupRenderer={groupRenderer}
                itemRenderer={itemRenderer}
                showCursorLine
                scrollRef={props.scrollRefD}
                headerLabelHeight={50}
                headerLabelGroupHeight={50}
                lineHeight={50}
                stackItems
                className={`p-sm-3`}
                resizeDetector={containerResizeDetector}
                onTimeChange={props.onTimeChange}
                onItemSelect={(itemId, e, time)=>props.clickItem(itemId)}
                onItemClick = {(itemId, e, time)=>props.clickItem(itemId)}
            >
              {/*<TimelineMarkers>*/}
              {/*  <TodayMarker>*/}

              {/*    {({ date }) =>*/}
              {/*        // date is value of current date. Use this to render special styles for the marker*/}
              {/*        // or any other custom logic based on date:*/}
              {/*        // e.g. styles = {...styles, backgroundColor: isDateInAfternoon(date) ? 'red' : 'limegreen'}*/}
              {/*        <div className={styles.todayMarker} />*/}
              {/*        // <div style={styles}/>*/}
              {/*    }*/}
              {/*  </TodayMarker>*/}
              {/*</TimelineMarkers>*/}
                <TimelineHeaders  className={`${styles.header} sticky`} style={{ color: 'black'}}>
                    <SidebarHeader>
                        {({ getRootProps }) => {
                            return <div className={`d-flex justify-content-center align-items-center ${styles.header}`} {...getRootProps()}>Rentals</div>;
                        }}
                    </SidebarHeader>
                    <DateHeader
                        unit="month"
                        labelFormat={"MMMM-YYYY"}
                        // height={21}
                        intervalRenderer={({ getIntervalProps, intervalContext, data }) => {
                          const text = intervalContext.intervalText && intervalContext.intervalText.split('-')
                          return <div {...getIntervalProps()} >
                            {text && text.length > 1 ? (
                              <>
                               
                                <div style={{textAlign:"center",color:"Black",backgroundColor:"#f3f6f5" ,fontSize:"14px", padding:"5px 0"}}>
                                  <span >{`${text[0]}, ${text[1]}`}</span>
                                </div>
                                </>
                              ) : intervalContext.intervalText}
                          </div>
                        }}
                    />
                    <DateHeader labelFormat="DD-ddd-MMM" unit="day" height={45}
                                intervalRenderer={({ getIntervalProps, intervalContext, data }) => {
                                    const text = intervalContext.intervalText && intervalContext.intervalText.split('-')
                                    return <div {...getIntervalProps()} className={
                                      (intervalContext.intervalText===moment().format("DD-ddd-MMM"))?
                                          `${styles.dataToday}`:`${styles.dataHeader}`}>
                                           
                                      {text && text.length > 1 ? (
                                            <div className={`d-flex flex-column`}>
                                                <span className={styles.dayHead}>{text[1]}</span>
                                                <span className={styles.dateHead} >{text[0]}</span>
                                            </div>
                                        ) : intervalContext.intervalText}
                                    </div>
                                }}
                    />
                </TimelineHeaders>
            </Timeline>
        )}
    </div>
  )
}

export default MyCalendar;